import { Component, ComponentFactoryResolver, OnDestroy } from '@angular/core';
import { ContentChildren, HostBinding, QueryList, ViewChild } from '@angular/core';
import { ElementRef, ViewContainerRef } from '@angular/core';
import { EventEmitter, Output } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/combineLatest';
import 'rxjs/add/operator/auditTime';

import { ChartAutoThemeComponent } from './chart/chart-auto-theme.component';
import { ChartComponentGenerated } from './chart.component.generated';
import { ConfigurationService, THROTTLE_MS } from './common/configuration.service';
import { InheritMetadata } from './common/inherit-metadata';
import { IntlService } from '@progress/kendo-angular-intl';
import { LegendItemClickEvent } from './events/legend-item-click-event';
import { SeriesItemComponent } from './chart/series-item.component';
import { ThemeService } from './common/theme.service';
import { TooltipTemplateService } from './common/tooltip-template.service';
import { TooltipPopupComponent } from './chart/tooltip/tooltip-popup.component';
import { CrosshairTooltipsContainerComponent } from './chart/tooltip/crosshair-tooltips-container.component';
import { hasParent } from './common/has-parent';

import * as charts from '@progress/kendo-charts';

import '@telerik/kendo-theme-default/styles/packages/charts';

/**
 * The root Chart component.
 *
 * @example
 * ```ts
 * import { Component } from '@angular/core';
 *
 * @@Component({
 *   selector: 'my-app',
 *   template: `
 *     <button (click)="toggleLegend()">Toggle Legend</button>
 *     <button (click)="toggleSeries()">Toggle Series</button>
 *     <kendo-chart>
 *       <kendo-chart-legend [visible]="legendVisible">
 *       </kendo-chart-legend>
 *       <kendo-chart-series>
 *         <kendo-chart-series-item *ngIf="seriesVisible" name="Series #1"
 *                                   type="line" [data]="[1, 2, 3]">
 *         </kendo-chart-series-item>
 *       </kendo-chart-series>
 *     </kendo-chart>
 *   `
 * })
 * class AppComponent {
 *   private legendVisible: boolean = true;
 *   private seriesVisible: boolean = true;
 *
 *   public toggleSeries(): void {
 *     this.seriesVisible = !this.seriesVisible;
 *   }
 *
 *   public toggleLegend(): void {
 *     this.legendVisible = !this.legendVisible;
 *   }
 * }
 *
 * ```
 */
@InheritMetadata()
@Component({
  providers: [ConfigurationService, TooltipTemplateService],
  selector: 'kendo-chart',
  template: `
    <div class="k-chart-surface" (mouseleave)="chartMouseleave($event)"></div>
    <kendo-chart-crosshair-tooltips-container>
    </kendo-chart-crosshair-tooltips-container>
    <kendo-chart-tooltip-popup (mouseleave)="tooltipMouseleave($event)">
    </kendo-chart-tooltip-popup>
  `
})
export class ChartComponent extends ChartComponentGenerated implements OnDestroy {
  /**
   * Fires when an legend item is clicked, before the selected series visibility is toggled.
   * Can be prevented.
   */
  @Output()
  public legendItemClick: EventEmitter<LegendItemClickEvent> = new EventEmitter();

  @HostBinding('class')
  protected get className(): string {
    return 'k-chart';
  }

  @ContentChildren(SeriesItemComponent, { descendants: true })
  protected seriesComponents: QueryList<SeriesItemComponent>;

  @ViewChild(TooltipPopupComponent)
  protected tooltipInstance: TooltipPopupComponent;

  @ViewChild(CrosshairTooltipsContainerComponent)
  protected crossahirTooltips: CrosshairTooltipsContainerComponent;

  private options: any;
  private theme: any = null;
  private subscription: Subscription;
  private suppressTransitions: boolean = false;

  constructor(public configurationService: ConfigurationService,
              public themeService: ThemeService,
              private element: ElementRef,
              private viewContainer: ViewContainerRef,
              private componentResolver: ComponentFactoryResolver,
              private intl: IntlService) {
    super(configurationService);
    this.loadTheme();
    this.refreshWait();

    charts.IntlService.register(intl);
  }

  public ngOnDestroy(): void {
    this.subscription.unsubscribe();

    if (this.instance) {
      this.unbindEvents();
      this.instance.destroy();
    }
  }

  protected init(): void {
    const element = this.element.nativeElement.querySelector('.k-chart-surface');
    this.instance = new charts.Chart(element, this.options, this.theme);
    this.bindEvents();
  }

  protected bindEvents(): void {
    super.bindEvents();

    this.instance.bind("legendItemClick", (e) => {
      const args = new LegendItemClickEvent(e);
      this.legendItemClick.emit(args);

      if (!args.isDefaultPrevented()) {
        const series = this.seriesComponents.toArray()[e.series.index];

        if (e.pointIndex === undefined) {
          series.toggleVisibility();
        } else {
          series.togglePointVisibility(e.pointIndex);
        }

        this.suppressTransitions = true;
      }
    });
    this.instance.bind('showTooltip', (e) => {
        if (!e.crosshair) {
            this.tooltipInstance.show(e);
        } else {
            this.crossahirTooltips.show(e);
        }
    });

    this.instance.bind('hideTooltip', (e) => {
        if (!e.crosshair) {
            this.tooltipInstance.hide();
        } else {
            this.crossahirTooltips.hide();
        }
    });
  }

  protected unbindEvents(): void {
    super.unbindEvents();
    this.instance.unbind("legendItemClick");
    this.instance.unbind('showTooltip');
    this.instance.unbind('hideTooltip');
  }

  protected refresh(): void {
    this.crossahirTooltips.createCrosshairTooltips(this.options);

    if (!this.instance) {
      this.init();
      return;
    }

    // Events are bound only if we have active observers.
    // Hence we need to re-check on each property change.
    this.unbindEvents();
    this.bindEvents();

    const transitions = this.options.transitions;
    if (this.suppressTransitions) {
      this.options.transitions = false;
    }

    this.instance.setOptions(this.options);

    if (this.suppressTransitions) {
      this.options.transitions = transitions;
      this.suppressTransitions = false;
    }
  }

  private refreshWait(): void {
    this.subscription = this.configurationService.onChange$.combineLatest(
      this.themeService.onChange$, (options, theme) => {
        this.options = options;
        this.theme = theme;
     }
    ).auditTime(THROTTLE_MS).subscribe((config: any) => {
        this.refresh();
    });
  }

  private loadTheme(): void {
    if (this.themeService.isLoaded()) {
      return;
    }

    this.viewContainer.createComponent(
      this.componentResolver.resolveComponentFactory(ChartAutoThemeComponent)
    );
  }

  private tooltipMouseleave(e: any): void { // tslint:disable-line:no-unused-variable
    const relatedTarget = e.relatedTarget;
    const chartElement = this.element.nativeElement;

    if (!relatedTarget || !hasParent(relatedTarget, chartElement)) {
        this.instance.hideElements();
    }
  }

  private chartMouseleave(e: any): void { // tslint:disable-line:no-unused-variable
    const relatedTarget = e.relatedTarget;
    if (!relatedTarget || !this.tooltipInstance.containsElement(relatedTarget)) {
        this.instance.hideElements();
    }
  }
}



